@import url(https://fonts.googleapis.com/css2?family=Architects+Daughter&family=Sansita+Swashed:wght@700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

html,body{font-family:"Architects Daughter",cursive}.header{height:10vh;background-image:linear-gradient(to right, #43e97b 0%, #38f9d7 100%);padding:15px 20px;border-bottom:5px dotted green}.recipeX{font-family:"Sansita Swashed",cursive;text-align:center}.App{min-height:100vh;background-image:linear-gradient(120deg, #84fab0 0%, #8fd3f4 100%)}.search-form{margin-top:10px;min-height:10vh;display:flex;justify-content:center;align-items:center}.search-bar{width:50%;border:none;padding:17px;border-radius:5px}.search-button{background:linear-gradient(to right, #43e97b 0%, #38f9d7 100%);border:3px inset grey;border-radius:5px;padding:10px 20px;margin-top:2px;margin-left:9px;color:#fff;font-size:20px;font-weight:800px;font-family:"Architects Daughter",cursive}.container{display:-ms-grid;display:grid;grid-gap:30px;gap:30px;-ms-grid-columns:1fr 1fr 1fr 1fr 1fr;grid-template-columns:1fr 1fr 1fr 1fr 1fr;margin:30px;justify-content:center;justify-items:center;-ms-grid-column-align:center;justify-self:center}.recipe-item{width:100%;height:100%;border:6px outset grey;background-color:#dcd9d4;background-image:linear-gradient(to bottom, rgba(255,255,255,0.5) 0%, rgba(0,0,0,0.5) 100%),radial-gradient(at 50% 0%, rgba(255,255,255,0.1) 0%, rgba(0,0,0,0.5) 50%);background-blend-mode:soft-light, screen}.recipe-item h1{text-align:center;cursor:pointer;font-size:1.7rem;min-height:60px}.recipe-item h1 a{text-decoration:none;color:black}.recipe-item img{border:2px outset grey;border-radius:10px;display:block;margin:10px auto;margin-bottom:30px;justify-content:center;justify-items:center;-ms-grid-column-align:center;justify-self:center;box-shadow:0 1px 2px #000;transition:box-shadow 0.3s ease-in-out}.recipe-item img:hover{box-shadow:0 5px 15px #000}.err{text-align:center;cursor:pointer;border:6px outset grey;border-radius:10px;background-color:#dcd9d4;background-image:linear-gradient(to bottom, rgba(255,255,255,0.5) 0%, rgba(0,0,0,0.5) 100%),radial-gradient(at 50% 0%, rgba(255,255,255,0.1) 0%, rgba(0,0,0,0.5) 50%);background-blend-mode:soft-light, screen;width:50%;margin:0 auto;padding:12px;box-shadow:0 1px 2px #000;transition:box-shadow 0.3s ease-in-out}.err:hover{box-shadow:0 5px 15px #000}@media only screen and (max-width: 1650px){.container{-ms-grid-columns:1fr 1fr 1fr 1fr;grid-template-columns:1fr 1fr 1fr 1fr}}@media only screen and (max-width: 1400px){.container{-ms-grid-columns:1fr 1fr;grid-template-columns:1fr 1fr}}@media only screen and (max-width: 700px){.container{-ms-grid-columns:1fr;grid-template-columns:1fr}.err{font-size:1.5rem;padding:18px}}@media only screen and (min-width: 1100px){.recipeX{font-size:40px}}
